  height: 100%;
}
html body {
  min-height: 100%;
  font-family: "Roboto", sans-serif;
}

html {
  height: 100%;
}
html body {
  min-height: 100%;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 1440px;
  box-sizing: border-box;
  padding: 7vw;
  margin: 0 auto;
}
@media (orientation: landscape) {
  .container {
    display: flex;
    align-items: center;
    padding: 7.1vw 4vw;
  }
}
@media (min-width: 780px) and (max-height: 415px) {
  .container {
    padding: 2.2vw 4vw;
  }
}
@media (min-width: 962px) {
  .container {
    padding: 5vw 8.2vw;
  }
}
.container .art {
  display: flex;
  max-width: 50%;
}
@media (orientation: landscape) {
  .container .art {
    display: flex;
    align-items: center;
  }
}
.container .art__img-cnt {
  flex-basis: 48%;
}
.container .art__img-cnt__img {
  display: block;
  width: 100%;
  max-height: 90vh;
}
.container .art__content {
  justify-content: space-between;
  text-align: center;
  flex-basis: 50%;
}
@media (orientation: landscape) {
  .container .art__content {
    text-align: left;
    margin-left: clamp(30px, 6.5vw, 95px);
  }
}
.container .art__tittle {
  font-size: clamp(24px, 7.7vmin, 42px);
  font-weight: 700;
  margin-top: 0.85em;
}
@media (orientation: landscape) {
  .container .art__tittle {
    margin-top: 0;
  }
}
@media (min-height: 600px) {
  .container .art__tittle {
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .container .art__tittle {
    font-size: clamp(24px, 6vmin, 42px);
    margin-bottom: 2em;
  }
}
.container .art__text {
  line-height: clamp(22px, 2.9vh, 24px);
  font-size: clamp(12px, 4vmin, 16px);
}
@media (min-width: 768px) and (orientation: portrait) {
  .container .art__text {
    padding: 0 70px;
  }
}
.container .art__btn {
  background-color: #E4C1AE;
  color: white;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  transition: background-color 0.2s;
  margin-top: 0.5em;
}
.container .art__btn:hover {
  cursor: pointer;
  background-color: #e7c7b6;
}
.container .modal-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(18, 18, 18, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
}
.container .modal-wrap__popup {
  box-sizing: border-box;
  position: relative;
  width: 85%;
  max-width: 920px;
  background-color: #bfb5b5;
  padding: clamp(10px, 6.5vmin, 40px) clamp(24px, 10vw, 95px);
}
.container .modal-wrap__tittle {
  font-size: clamp(24px, 7.7vmin, 42px);
  font-weight: 700;
  margin: 16px 0;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .container .modal-wrap__tittle {
    font-size: clamp(24px, 5.5vmin, 42px);
  }
}
.container .modal-wrap__info {
  line-height: 24px;
  margin-top: clamp(16px, 2vw, 24px);
  margin-bottom: clamp(16px, 2vw, 24px);
  cursor: pointer;
}
.container .modal-wrap__closeBtn {
  position: absolute;
  top: 30px;
  background-color: rgba(15, 15, 15, 0.5);
  color: white;
  right: clamp(30px, 5vw, 35px);
  transition: background-color 0.2s;
  width: 21px;
  height: 21px;
}
.container .modal-wrap__closeBtn:hover {
  cursor: pointer;
  background-color: rgba(92, 92, 92, 0.5);
}
.container .modal-wrap__resetBtn {
  margin: 10px 0 16px 0;
  background-color: magenta;
  color: #121212;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  transition: background-color 0.2s;
}
.container .modal-wrap__resetBtn:hover {
  cursor: pointer;
  background-color: #ff99ff;
}